body {
  color: #333;
  background-color: #f0f2f5;
  margin: 0;
  padding: 20px;
}

h1, h2 {
  text-align: center;
  color: #2c3e50;
}

.form {
  background-color: #fff;
  border-radius: 12px;
  max-width: 600px;
  margin: 0 auto 30px;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

input, textarea {
  resize: vertical;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 15px;
  padding: 12px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #2980b9;
}

.posts, .external-comments {
  max-width: 600px;
  margin: 0 auto 20px;
  padding: 0;
  list-style: none;
}

.post, .comment-item {
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 15px 20px;
  box-shadow: 0 1px 6px #00000014;
}

.post-title {
  color: #34495e;
  margin-top: 0;
  font-size: 20px;
}

.post-text {
  margin: 10px 0;
  line-height: 1.5;
}

.btn-delete {
  color: #fff;
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 6px;
  padding: 8px 14px;
}

.btn-delete:hover {
  background-color: #c0392b;
}

.load-more-btn {
  margin: 0 auto 40px;
  display: block;
}
